
body {
  font-family: Arial, sans-serif;
}

.container {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  /* color: #44475b; */
  border-radius: 5px;
  color: transparent;
  -webkit-background-clip: text;
  font-size: 33px;
  font-weight: bolder;
  background-color: aliceblue;


}
p {
  margin-top: 19px;
}

label {
  display: flex;
  margin: 10px 25px;
  color: #047761;
  align-items: center;
  justify-content: space-between;
}

input {
  width: fit-content;
  padding: 0px 5px;
  margin: 4px 8px;
  box-sizing: border-box;
  /* border-color: whitesmoke; */
  display: flex;
  text-align: right;
  border: none;
  outline: 0px;
  opacity: 1;
  font-weight: 700;
  background: #c4fff4;
  caret-color: #00b8f5;
  align-items: center;
  height: 30px;
  border-radius: 2px;

}

.header-container {
  margin: 0px 0px;
  /* border-radius: 20px; */
  font-family: none;
  font-size: 15px;
  padding: 1px;
  background-color: #08987d;
}

.calculator-container {
  margin: 32px 12px;
  border-radius: 20px;
  font-family: none;
  font-size: 15px;
  padding: 1px;
  background-color: #edfffc;
}

/* .calculator-container:hover {
  background-color: #e0e0e0;
  cursor: pointer;
} */

.result-container {
  margin: 32px 12px;
  border-radius: 20px;
  font-family: none;
  font-size: 15px;
  padding: 1px;
}

.result-Value {
  display: flex;
  background-color: #08987d;
  margin: 10px 0px 0px 0px;
  border-radius: 5px;
}

strong {
  color: #f3eeee;
  padding: 10px 10px 10px 10px;
  margin: 10px -5px 5px 15px;
  font-family: none;
}

